body{
    background: #4a5660;

}

header{
    background: #718493;
}
#logo img{
   margin-top: 60px;
   margin-bottom: 60px;
}

#logo{
    width: 30%;
    margin: 0 auto;
}
.boton{
    background: transparent;
    border: 3px solid white;
    border-radius: 10px;
    width: 100%;
    height: 50px;
    color: white;
    font-size: 17px;
    margin-bottom: 29px;
}
.boton:hover{
    background: white;
    color: #4a5660;
}
.text-navlink{
    color: #b2c7d8;
}
.text-navlink:hover{
    color: white;
}
#logo .input{
    width: 100%;
}

#logo #n{
    height: 50px;
    border-radius: 10px;
}

h4{
    color: white;
    margin: 15px;
    font-weight: bold;
}

.new{
    width:20%;
    margin: 15px;
    height: 50px;
    background:#718493;
    text-align: center;
    border-radius: 10px;
    padding-top: 10px;
    cursor: pointer;
}


